@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;500&display=swap);

*{
  margin: 0;
  padding: 0;
}
.view-container{
  height: 96%;
  width: 99%;
  border: 1px solid #444;
  box-shadow: 0 50px 50px #444;
}

.block-left{
  background-color: hsla(201, 100%, 6%, 0.6);
  width: 30%;
  height: 100vh;
  float:left;
}
.block-right{
  width:70%;
  height:100vh;
  background: #ADA996;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to top, #EAEAEA, #DBDBDB, #F2F2F2, #ADA996); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  float:left;
}

input[type=text]{
  width:60%;
  border:2px solid #aaa;
  border-radius:4px;
  margin:8px 0;
  outline:none;
  padding:8px;
  box-sizing:border-box;
  transition:.3s;
}

input[type=text]:focus{
  border-color:#000;
  box-shadow:0 5px 18px 0 #000;
}

.text-center{
  margin:auto;
  text-align: center;
}



.login-container {
  padding: 50px 30px;
}

.login-title {
  color: #fff;
  text-align: center;
  margin: 0;
  margin-bottom: 40px;
  font-size: 2.5em;
  font-weight: normal;
}

.input-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.input-group label {
  color: #fff;
  font-weight: lighter;
  font-size: 1.5em;
  margin-bottom: 7px;
}

.input-group input {
  font-size: 1.5em;
  padding: 0.1em 0.25em;
  background-color: hsla(201, 100%, 91%, 0.3);
  border: 1px solid hsl(201, 100%, 6%);
  outline: none;
  width: 100%;
  border-radius: 5px;
  color: #fff;
  font-weight: lighter;
}

.input-group input:focus {
  border: 1px solid hsl(201, 100%, 50%);
}

.login-button {
  padding: 10px 30px;
  width: 100%;
  border-radius: 5px;
  background: hsla(201, 100%, 50%, 0.1);
  border: 1px solid hsl(201, 100%, 50%);
  outline: none;
  font-size: 1.5em;
  color: #fff;
  font-weight: lighter;
  margin-top: 20px;
  cursor: pointer;
}

.login-button:hover {
  background-color: hsla(201, 100%, 50%, 0.3);
}

.login-button:focus {
  background-color: hsla(201, 100%, 50%, 0.5);
}



/* MEDIA QUERIES FOR 545px <= Resolutions */ 

@media (max-width:545px){
  .block-left{
    display:block;
    width:100%;
    height: 300px;
  }
  .block-right{
    display:block;
    width:100%;
  }
}
